import * as React from "react";
import { Breadcrumbs, Button, ButtonGroup, FormControl, Hidden, IconButton, List, ListItem, ListItemSecondaryAction, ListItemText, Modal, Paper, Select, Typography } from "@material-ui/core";
import Navbar from "../../../components/ui/Navbar";
import { Link } from "gatsby";
import { useFetcher } from "../../../components/ajax";
import AddIcon from '@material-ui/icons/Add';
import { navigate } from "gatsby";
import { useUserInfo } from "../../../components/save";
import Defer from "../../../components/ui/Defer";
import { buildSearchParam } from "../../../components/util";
import Infra from '@material-ui/icons/AccountBalance';
import Serv from '@material-ui/icons/SupervisedUserCircle';
import Umkm from '@material-ui/icons/Storefront';
import Trans from '@material-ui/icons/DriveEta';

export const categories = [{
    type: 'infrastruktur',
    desc: 'Pelaporan atau saran mengenai infrastruktur publik, bangunan umum, dsb',
    title: 'Infrastruktur Publik',
    icon: Infra,
}, {
    type: 'layanan',
    desc: 'Pelaporan atau saran mengenai layanan desa, program kerja desa, dsb',
    title: 'Alur Layanan Desa',
    icon: Serv,
}, {
    type: 'umkm',
    desc: 'Pelaporan atau saran mengenai bisnis mikro, pemberdayaan UMKM, dsb',
    title: 'Pemberdayaan UMKM',
    icon: Umkm,
}, {
    type: 'transportasi',
    desc: 'Pelaporan atau saran mengenai akses transportasi, rute jalan, dsb',
    title: 'Akses Transportasi',
    icon: Trans,
}];

export const keyedCategories = categories.reduce((a, b) => {
    a[b.type] = b;
    return a;
}, {})

const Page = () => {
    const user = useUserInfo();
    const [status, setStatus] = React.useState('');
    const [category, setCategory] = React.useState('');
    const list = useFetcher('/desa/ask/?' + buildSearchParam({ category, status }));
    return (
        <div>
            <Navbar />
            <List>
                <ListItem>
                    <Breadcrumbs>
                        <Link color="inherit" to="/user/">
                            Layanan Desa
                        </Link>
                        <Typography color="textPrimary">Layanan Komplain</Typography>
                    </Breadcrumbs>
                    <ListItemSecondaryAction>
                        <Button startIcon={<AddIcon />} onClick={() => navigate('/desa/komplain/post')} variant="contained" color="primary">
                            Ajukan Komplain / Saran
                        </Button>
                    </ListItemSecondaryAction>
                </ListItem>
                <ListItem divider>
                    <ListItemText primary="Filter" />
                    <ListItemSecondaryAction>
                        <FormControl style={{ minWidth: '20vw' }}>
                            <Select native value={category} onChange={(x) => { setCategory(x.target.value); }}>
                                <option value="">Semua Kategori</option>
                                {categories.map(x => <option key={x.type} value={x.type}>{x.title}</option>)}
                            </Select>
                        </FormControl>
                        <FormControl style={{ minWidth: '20vw' }}>
                            <Select native value={status} onChange={(x) => { setStatus(x.target.value); }}>
                                <option value="">Semua Status</option>
                                <option value="open">Belum Direspon</option>
                                <option value="responded">Sudah Ditanggapi</option>
                                <option value="done">Sudah Diterapkan</option>
                            </Select>
                        </FormControl>
                    </ListItemSecondaryAction>
                </ListItem>
                <Defer data={list}>
                    {() => (
                        list.data.map(item => (<ListItem key={item.id} button onClick={() => navigate('/desa/komplain/detail?id='+item.id)}>
                            {keyedCategories[item.category] && React.createElement(keyedCategories[item.category].icon, { style: { fontSize: '2em', marginRight: 15 } })}
                            <ListItemText primary={item.message} secondary={item.status+', '+item.created_at} />
                        </ListItem>))
                    )}
                </Defer>
            </List>
        </div>
    );
}

export default Page
